/**
 * <p
 *    film
 * </p>
 */

import api from './index'

const path = {
  inTheaters: 'v2/movie/in_theaters',     // 
  comingSoon: 'v2/movie/coming_soon',     // 
  usBox: 'v2/movie/us_box',               // 
  top250: 'v2/movie/top250',              // Top250
  weekly: 'v2/movie/weekly',              // 
  newMovies: 'v2/movie/new_movies'        // 
}

const inTheaters = (city, count, start) => api.get(path.inTheaters, {
  params: {
    city: city,
    count: count,
    start: start
  }
})

const comingSoon = city => api.get(path.comingSoon)

const usBox = city => api.get(path.usBox)

const top250 = city => api.get(path.top250)

const weekly = city => api.get(path.weekly)

const newMovies = city => api.get(path.newMovies)

export default {
  inTheaters,
  comingSoon,
  usBox,
  top250,
  weekly,
  newMovies
}



